@import url('https://fonts.googleapis.com/css?family=Roboto');
.profile{
    color: black;
    background-color: white;
    border-radius: 12px;
    margin-top: 20px;
    padding-top: 1%;
    padding-right: 3%;
    padding-bottom: 3%;
    padding-left: 3%;
    box-shadow: 0px 20px 80px 0px rgba(153, 153, 153, 0.6)
}
.logo{
    border-image-slice: 30%;
    max-width: 100%;
}

.description{
    color: black;
}
h6{
    font-size: 14px;
    font-family:'Roboto', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2.1px;
    font-weight: normal;
    margin-bottom: 12px;
}
h3{
    text-transform: uppercase;
    margin-bottom: 15px;
    color: #222222;
    font-size: 45px;
    line-height: 55px;
    margin-top: 10px;
    margin-bottom: 10px;
}
h4{
    font-size: 16px;
    font-weight: 500;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    margin-bottom: 20px;
    color: #222222;
    margin-top: 12px;
    
}
p{
    font-family: "Roboto", sans-serif;
    max-width: 540px;
    color: #777777;
    margin-bottom: 40px;
}
.list{
    list-style: none;
    margin: 0px;
    padding: 0px;
}
li{
    position: relative;
    padding-left: 40px;
    font-size: 16px;
    color: #777777;
    
}
h2{
    margin-top: 5px;
    margin-bottom: 5px
}



